<template>
    <li class="item">
        <a href="javascript:void(0)" v-on:click.prevent="page(to)">
            <div>
                <slot></slot>
            </div>
        </a>
    </li>
</template>
<script>
export default {
    name: "nav-item",
    props: {
        to: {
            type: Number,
            default: 1
        }
    },
    data(){
        return {}
    },
    methods: {
        // @page : void - @to
            // klik event
        // emit
            // @topage(@to)
        page: function(to){
            this.$emit("topage", to)
        }
    }
}
</script>