<template>
    <div class="modal-backdrop" style="background-color: rgba(0,0,0,.5)" @click="backdropclick($event)" :id="id">
        <div class="modal-dialog modal-dialog-scrollable modal-dialog-centered" :class="modalclass">
            <transition :name="transition">
                <div class="modal-content border-0 shadow" :class="modalcontentclass" v-if="show">
                    <slot name="header"></slot>
                    <div class="modal-body">
                        <slot></slot>
                    </div>
                    <slot name="footer"></slot>
                </div>
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    install: function(Vue){
        var data = new Vue({
            data: {
                modals: [],
            },
            methods: {
                add: function(v){
                    this.modals.unshift(v);
                    return v.id;
                },
                get: function(id){
                    let i = this.modals.findIndex(e => e.id == id);
                    return this.modals[i];
                },
                destroy: function(id){
                    let i = this.modals.findIndex(e => e.id == id);
                    this.modals.splice(i, 1);
                },
                clear: function(){
                    this.modals = this.notifications.map(e => {
                        e._show = false;
                        return e;
                    });
                },
                count: function(){
                    return this.modals.length;
                },
                remove: function(id){
                    this.destroy(id);
                },
                count: function(){
                    return this.modals.length;
                },
            },
            created: function(){
                window.s = this.modals;
            }
        });
        Vue.prototype.$modals = data;
    },
    props: {
        size: {
            type: String,
            default: "medium",
        },
        theme: {
            type: String,
        },
        modalclose: {},
        transition: {
            type: String,
            default: 'fade'
        }
    },
    computed: {
        modalclass(){
            return [
                this.sizemodal(),
            ]
        },
        modalcontentclass(){
            return [
                this.thememodal()
            ]
        }
    },
    data: function(){
        return {
            show: false,
            id: null,
        }
    },
    methods: {
        sizemodal: function(){
            switch(String(this.size).toLowerCase()){
                case "small":
                case "sm":
                    return "modal-sm";
                    break;
                case "medium":
                case "md":
                default:
                    return "modal-md";
                    break;
                case "large":
                case "lg":
                    return "modal-lg";
                    break;
            }
        },
        thememodal: function(){
            switch(String(this.theme).toLowerCase()){
                case "dark":
                    return "bg-dark text-light";
                    break;
                case "light":
                    return "bg-light text-dark";
                default:
                    return "";
                    break;
            }
        },
        backdropclick: function(e){
            console.log("clicked", document.querySelector(`div#${this.id}.modal-backdrop`));
            if(e.target == document.querySelector(`div#${this.id}.modal-backdrop`)){
                console.log("im in");
                this.$emit('modalclose', true);
            }
        }
    },
    mounted: function(){
        this.$nextTick(()=>{
            
            let now = new Date();
            this.id = `modal--${now.valueOf()}`;

            document.body.classList.add("modal-open");
            this.show = true;
            if(this.$modals != undefined)
                this.$modals.add(this);
        })
    },
    destroyed: function(){
        if(this.$modals != undefined){
            this.$modals.remove(this.id);
            if(this.$modals.count() > 0)
                return;
        }
        document.body.classList.remove("modal-open");
    }
}
</script>