<template>
    <div class="vertical-tab" :class="{reverse: navbarRight, dark: navbarTheme}">
        <div class="navbar" :style="{height: `${navbarHeight}px`}">
            <div class="navbar-wrapper">
                <slot name="navbar"></slot>
            </div>
        </div>
        <div class="content">
            <slot></slot>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        position: {
            type: String,
            default: "left"
        },
        theme: {
            type: String,
            default: "light"
        }
    },
    data(){
        return {
            navbarHeight: 0
        }
    },
    computed: {
        navbarRight(){
            return String(this.position).toLowerCase() == "right" ? true : false
        },
        navbarTheme(){
            return String(this.position).toLowerCase() == "dark" ? "dark" : "light"
        }
    },
    methods: {
        updateverticalheight: function(){
            this.navbarHeight = this.$el.querySelector(".navbar-wrapper").offsetWidth;
        }
    },
    created(){
        window.addEventListener("resize", this.updateverticalheight);
        window.addEventListener("scroll", this.updateverticalheight);
    },
    mounted(){
        this.updateverticalheight()
    },
    destroyed(){
        window.removeEventListener("resize", this.updateverticalheight);
        window.removeEventListener("scroll", this.updateverticalheight);
    }
}
</script>
<style lang="scss" scoped>
    .vertical-tab{
        display: flex;
        flex-direction: row;
        &.reverse{
            flex-direction: row-reverse;

            & > .content{
                padding-left: unset;
                padding-right: 1rem;
            }
        }
        
        & > .navbar{
            width: 80px;
            min-width: 40px;
            position: relative;

            & > .navbar-wrapper{
                transform: rotateZ(-90deg) translateX(0%) translateY(0%);
                position: absolute;
                transform-origin: left top;
                display: flex;
                flex-direction: row-reverse;
                top: 100%;
                left: 0px;
                
                & .nav-item{
                    &.active{
                        background: rgba(lighten(#343a40, 50%), .3);
                        border-radius: 10rem;
                    }
                }
            }
        }
        & > .content{
            flex: 1 1 auto;
            padding-left: 1rem;
        }
    }
</style>