<template>
    <div class="position-relative">
        <div class="table-responsive">
            <table class="table rounded table-hover" :style="{opacity: loading && transparent ? .5 : 1, filter: loading && blur ? 'blur(2px)' : 'unset'}">
                <slot name="header">
                    <thead v-if="headers.length" class="shadow-sm">
                        <th v-for="(h, i) in tableheader" :key="h.label" :class="[h.className]" class="border-0 list-group-item-action w-auto" :style="{'border-bottom': isSort(i).status && color ? `2px solid ${color}!important` : null}">
                            <div class="small py-2 position-relative">
                                <div class="d-flex">
                                    <button class="badge badge-pill btn btn-link text-muted text-decoration-none px-0 py-2" :class="[ h.classLabel , sorterClass(i)]" style="font-size: 100%" @click="toggleSort(i)">
                                        <div class="d-flex font-weight-normal">
                                            <div>
                                                {{ h.label }}
                                            </div>
                                            <div class="text-muted">
                                                <div class="pl-2 small" :style="{'color': !isSort(i).status ? 'rgba(0,0,0,.15)' : null }" v-if="h.sorter">
                                                    <i class="fa" :class="{'fa-arrow-down': isSort(i).status && isSort(i).type == 'asc', 'fa-arrow-up': isSort(i).status && isSort(i).type == 'desc', 'fa-sort': !isSort(i).status}"></i>
                                                </div>
                                            </div>
                                        </div>
                                    </button>
                                    <div class="ml-auto d-flex flex-column justify-content-center position-relative pr-2" v-if="h.filter">
                                        <transition name="fly-down">
                                            <div class="position-absolute mr-1 d-none d-md-block" style="right: 1.8rem;" v-if="labelfilter">
                                                Saring
                                            </div>
                                        </transition>
                                        <button class="badge badge-pill btn btn-link text-decoration-none font-weight-bold px-2 py-2 position-relative" style="font-size: 100%; color: rgba(0,0,0,.25)" @click="emitFilter(i)">
                                            <div class="small">
                                                <i class="fa fa-filter"></i>
                                            </div>
                                            <div class="badge badge-success rounded-0 position-absolute d-block p-0" style="left: 100%; top: 100%; transform: translate3d(-200%, -200%, 0); height: .25rem; width: .25rem"></div>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </th>
                    </thead>
                </slot>
                <slot name="body">
                    <tbody>
                        <tr>
                            <td colspan="100">
                                <div class="alert bg-warning shadow" v-if="search.length">
                                    <div class="d-flex">
                                        <div class="mr-3">
                                            <i class="fa fa-search"></i>
                                        </div>
                                        <div class="small">
                                            Tidak ada data dengan hasil pencarian `
                                            <span class="font-weight-bold">
                                                {{ search }}
                                            </span>
                                            `.
                                        </div>
                                    </div>
                                </div>
                                <div v-else>
                                    <div class="small text-muted">
                                        Tidak ada data.
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </slot>
                <slot name="header">
                    <thead v-if="headers.length" class="">
                        <th v-for="(h, i) in tableheader" :key="h.label" :class="[h.className]" class="border-0 list-group-item-action w-auto" :style="{'border-top': isSort(i).status && color ? `2px solid ${color}!important` : null}">
                            <div class="small py-2 position-relative">
                                <div class="d-flex">
                                    <button class="badge badge-pill btn btn-link text-muted text-decoration-none px-0 py-2" :class="[ h.classLabel , sorterClass(i)]" style="font-size: 100%" @click="toggleSort(i)">
                                        <div class="d-flex font-weight-normal">
                                            <div>
                                                {{ h.label }}
                                            </div>
                                            <div class="text-muted">
                                                <div class="pl-2 small" :style="{'color': !isSort(i).status ? 'rgba(0,0,0,.15)' : null }" v-if="h.sorter">
                                                    <i class="fa" :class="{'fa-arrow-down': isSort(i).status && isSort(i).type == 'asc', 'fa-arrow-up': isSort(i).status && isSort(i).type == 'desc', 'fa-sort': !isSort(i).status}"></i>
                                                </div>
                                            </div>
                                        </div>
                                    </button>
                                    <div class="ml-auto d-flex flex-column justify-content-center position-relative pr-2" v-if="h.filter">
                                        <transition name="fly-down">
                                            <div class="position-absolute mr-1 d-none d-md-block" style="right: 1.8rem;" v-if="labelfilter">
                                                Saring
                                            </div>
                                        </transition>
                                        <button class="badge badge-pill btn btn-link text-decoration-none font-weight-bold px-2 py-2 position-relative" style="font-size: 100%; color: rgba(0,0,0,.25)" @click="emitFilter(i)">
                                            <div class="small">
                                                <i class="fa fa-filter"></i>
                                            </div>
                                            <div class="badge badge-success rounded-0 position-absolute d-block p-0" style="left: 100%; top: 100%; transform: translate3d(-200%, -200%, 0); height: .25rem; width: .25rem"></div>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </th>
                    </thead>
                </slot>
            </table>
        </div>
        <transition :name="loadingtransition">
            <div class="position-absolute" style="top: 50%; left: 50%; transform: translateX(-50%) translateY(-50%)" v-if="loading">
                <slot name="loading">
                    <div class="small card shadow-lg border-0">
                        <div class="card-body">
                            Sedang Memuat..
                        </div>
                    </div>
                </slot>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    props: {
        "headers": {
            type: Array,
            default: function(){
                return []
            },
            required: false,
        },
        "loading": {
            type: Boolean,
            required: false,
            default: false
        },
        "loadingtransition": {
            type: String,
            required: false,
            default: ''
        },
        "blur": {
            type: Boolean,
            required: false,
            default: false
        },
        "transparent": {
            type: Boolean,
            required: false,
            default: false
        },
        "headerbottom": {
            type: Boolean,
            required: false,
            default: false
        },
        "search": {
            type: String,
            default: "",
            required: false,
        },
        "sorter": {},
        "order": {},
        "defaultsort": {
            type: Number,
            default: 0,
            required: false
        },
        "labelfilterstatus": {
            type: Boolean,
            default: false
        },
        color: {
            type: String,
            default: null
        }
    },
    data(){
        return {
            sort: {
                column: "",
                type: "asc",
            },
            labelfilter: false,
            timeout: {
                labelfilter: null
            }
        }
    },
    computed: {
        tableheader: function(){
            return this.headers.map((e, i) => e);
        },
    },
    methods: {
        isSort: function(i){
            let header = this.tableheader[i];
            return {
                status: this.sort.column == header.column,
                type: this.sort.type
            };
        },
        toggleSort: function(i){
            let header = this.tableheader[i];
            if(header.sorter){
                this.sort.column = header.column;
                this.sort.type = this.sort.type == 'asc' ? 'desc' : 'asc';
                let obj = Object.assign({}, this.sort);
                this.$emit("sort", obj);
            }
        },
        sorterClass: function(i){
            return {

            };
        },
        emitFilter: function(i){
            this.$emit("onfilter", this.headers[i]);
        },
    },
    watch: {

    },
    created: function(){
        this.sort.column = this.tableheader[this.defaultsort].column
    },
    mounted: function(){
        this.$nextTick(()=>{
            if(this.labelfilterstatus){
                this.labelfilter = true;
                this.timeout.labelfilter = setTimeout(()=>{
                    clearTimeout(this.timeout.labelfilter);
                    this.labelfilter = false;
                }, 2500);
            }
        })
    }
}
</script>
<style lang="scss">
    .table{
        td{
            border-bottom: 1px solid #dee2e6;
        }
    }
</style>