<template>
    <span :class="iconclass" class="_container" :style="iconstyle">
        <transition name="decoration">
            <span v-if="state"></span>
        </transition>
        <slot></slot>
    </span>
</template>
<script>
export default {
    props:{
        state: {
            type: Boolean,
            default: false,
        },
        color: {
            type: String
        },
        animate: {
            type: String
        }
    },
    computed: {
        iconclass(){
            return this.state ? this.animate : null;
        },
        iconstyle(){
            return this.state ? {color: this.color ? this.color : null} : null;
        },
    },
    methods: {

    }
}
</script>
<style lang="scss" scoped>
    @import './style';
</style>