<template>
    <ul>
        <li class="list-unstyled" v-for="(item, i) in itemsChild" :key="`id${i}`">
            <checklist-item :item="item" @onchange="onItemChange($event)"></checklist-item>
        </li>
    </ul>
</template>
<script>

import CheckListItem from './CheckListItem';

export default {
    name: "CheckListSubItem",
    props: {
        items: {
            type: Array,
            required: true
        },
        transition: {
            type: String,
            default: 'fade',
        }
    },
    components: {
        'checklist-item': CheckListItem
    },
    data: function(){
        return {
            checked: [],
            itemCheckedStore: [],
        }
    },
    computed: {
        itemsChild: {
            set: function(v){
                this.itemCheckedStore = v;
            },
            get: function(){
                return this.items.slice();
            }
        },
    },
    methods: {
        onItemChange: function(e){
            var newObj = Object.assign({}, e);
            let arr = this.itemsChild.map((o, i)=>{
                let obj = Object.assign({}, o);
                return obj.label == newObj.label ? newObj : obj;
            });
            this.$emit("onitemchange", arr.slice());
        }
    },
    created: function(){
    },
    updated: function(){
    }
}
</script>