<template>
    <transition :name="transition">
        <div class="position-fixed col-lg-4 col-md-5 col-sm-7 p-0" style="bottom: 0px; right: 0px; z-index: 2" v-if="$notification.count()">
            <transition-group :name="transition" tag="div" :css="true" @after-leave="destroyNotif" class="p-2">
                <template v-for="n in $notification.notifications">
                    <notification :key="n.id" :notif="n" v-if="n._show" :data-id="n.id"></notification>
                </template>
            </transition-group>
        </div>
    </transition>
</template>
<script>
import Container from './Container.vue'

export default {
    install: function(Vue){
        var data = new Vue({
            data: {
                notifications: [],
                events: {
                    scroll: {
                        x: 0,
                        y: 0,
                    }
                }
            },
            computed: {
            },
            methods: {
                add: function(v){
                    let now = new Date();
                    v._show = true;
                    v.id = now.valueOf();
                    this.notifications.unshift(v);
                    return v.id;
                },
                get: function(id){
                    let i = this.notifications.findIndex(e => e.id == id);
                    return this.notifications[i];
                },
                destroy: function(id){
                    let i = this.notifications.findIndex(e => e.id == id);
                    this.notifications.splice(i, 1);
                },
                clear: function(){
                    this.notifications = this.notifications.map(e => {
                        e._show = false;
                        return e;
                    });
                },
                remove: function(id){
                    let i = this.notifications.findIndex(e => e.id == id);
                    this.notifications[i]._show = false;
                },
                count: function(){
                    return this.notifications.length;
                },
                setScrollEvent: function(e) {
                    this.events.scroll.y = window.pageYOffset;
                    this.events.scroll.x = window.pageYOffset;
                }
            },
            created: function(){
                window.addEventListener("scroll", this.setScrollEvent);
            }
        });
        Vue.prototype.$notification = data;
    },
    props: {
        transition: {
            type: String,
            required: false,
            default: '',
        }
    },
    data: function(){
        return {
        }
    },
    methods: {
        destroyNotif: function(i, done){
            this.$notification.destroy(i.dataset.id);
        },
    },
    components: {
        'notification': Container
    },
}
</script>