<template>
    <transition-group tag="ul" class="pagination" name="fly-down" mode="in-out">
        <navitem key="first" v-if="!firstPage" :to="1" v-on:topage="page($event)">
            <first-icon/>
        </navitem>
        <navitem key="prev" :to="pagination_state.current_page - 1" v-on:topage="page($event)">
            <previous-icon :state="!firstPage"/>
        </navitem>
        <template v-for="i in generated_page">
            <navitem :key="i" :to="i" v-on:topage="page($event)"
                :class="[{active: i == pagination_state.current_page}]">
                <span>
                    {{ i }}
                </span>
            </navitem>
        </template>
        <navitem key="next" :to="pagination_state.current_page + 1" v-on:topage="page($event)">
            <next-icon :state="!lastPage"/>
        </navitem>
        <navitem key="last" v-if="!lastPage" :to="pagination_state.last_page" v-on:topage="page($event)">
            <last-icon :state="!lastPage"/>
        </navitem>
    </transition-group>
</template>
<script>
import validator from './libs/validator'
import generator from './libs/generator'
import propsoptional from './libs/props-optional'

//components
import navitem from './components/NavItem'
//icons
import first from './components/icons/first'
import previous from './components/icons/previous'
import next from './components/icons/next'
import last from './components/icons/last'

export default {
    name: "edd-pagination",
    components: {
        navitem: navitem,
        //icons
        'first-icon': first,
        'previous-icon': previous,
        'next-icon': next,
        'last-icon': last,
    },
    props: {
        // @pagination : object     - menerima objek dengan key wajib:
            /*
            **  @current_key : int  - nomor halaman yang sedang aktif
            **  @last_page : int    - nomor halaman terakhir dari paginasi
            */
        'pagination': {
            current_page: {
                type: Number,
                required: true
            },
            last_page: {
                type: Number,
                required: true
            },
        },
        /*
        ** props-optional

        ** @transition : string
        */
        ...propsoptional
    },
    data(){
        return {

        }
    },
    computed: {
        // @pagination_state : object
            // hanya mereturn hasil dari @pagination props
            // adanya property ini hanya berjaga jaga ketika
            // keys yang diterima pada props @pagination
            // tidak dipenuhi
        pagination_state: function(){
            return {
                current_page: this.pagination.current_page != undefined ? this.pagination.current_page : 1, 
                last_page: this.pagination.last_page != undefined ? this.pagination.last_page : 1
            }
        },
        // @lastPage : bool
            // property yang mereturn hasil boolean jika
            // halaman yang aktif @pagination_state.current_page
            // sama dengan atau lebih besar dari halaman .terakhir
        lastPage: function(){
            return this.pagination_state.current_page >= this.pagination_state.last_page;
        },
        // @firstPage : bool
            // property yang mereturn hasil boolean jika
            // halaman yang aktif @pagination_state.current_page
            // dibawah atau sama dengan halaman awal
        firstPage: function(){
            return this.pagination_state.current_page <= this.start
        },

        /**
         *  @generated_page : array
         */
        generated_page: function(){
            let n = this.max <= this.pagination.last_page ? this.max : this.pagination.last_page
            let pages = Array(n)
            var {start} = this.getPages(n)
            pages = pages.fill(1).map((e, i) => start + i)
            return pages
        }
    },
    methods: {
        // @page() : void
            // page adalah event yang akan ditriger ketika salah satu
            // nomor halaman diklik.
            // dengan mengecek apakah nomor halaman yang diklik adalah
            // nomor diantara 1 hingga halaman terakhir @pagination_state.last_page
            // dan mentriger event @onpage, sebaliknya event @onpageinvalid
        // $emit
            // @onpage
            // @onpageinvalid
            // @page
            // @pageinvalid
        //deprecated
            // @onpage
            // @onpageinvalid
        page: function(page){
            if(this.valid(page)){
                this.$emit('onpage', {page: page})
                this.$emit('page', {page: page})
            }
            else{
                this.$emit('onpageinvalid', {page: page})
                this.$emit('pageinvalid', {page: page})
            }
        },

        /**
         * libs/generator.js

         * @getPages(int n)
         */
        ...generator,

        /*
        ** libs/validator.js

        ** @valid(int page) : bool
        ** @biggerOf(int page, int range)   : bool
        ** @lessOf(int page, int range)   : bool
        ** @last(int page)  : bool
        ** @last(int page)  : bool
        ** @beetween(int page, int range)   : bool
        ** @begin(int page) : bool
        */
        ...validator
    },
    mounted: function(){
        
    }
}
</script>
<style lang="scss">
    @import './styles/main';
</style>
