<template>
    <div class="position-relative w-100 no-select">
        <div>
            <input type="text" :name="name" class="d-none" v-model="value" required>
        </div>
        <div class="">
            <transition mode="out-in">
                <div class="form-control form-control-sm" @click="toggleDropdown()" :key="dropdown.status" style="height: auto; background: none">
                    <div class="d-flex w-100" v-if="items != null">
                        <transition name="fly-down-absolute" tag="div" class="position-relative">
                            <select-item v-if="getItemKey(selected)" :item="selected" :img="img"/>
                            <div v-else>
                                Pilih...
                            </div>
                        </transition>
                        <div class="small px-3 ml-auto d-flex flex-column justify-content-center">
                            <i class="fa fa-chevron-down"></i>
                        </div>
                    </div>
                    <div v-else>
                        <slot></slot>
                    </div>
                </div>
            </transition>
        </div>
        <transition :name="transition">
            <div class="dropdown-menu w-100 show p-0 border-0" :class="{'position-absolute': absolute, 'position-relative py-3': !absolute}" style="z-index: 1; left: 50%; top: 50%; transform: translateX(-50%); min-width: 260px;" v-if="dropdown.status">
                <div class="card border-0 shadow">
                    <div class="card-block">
                        <div class="form-group p-3 m-0">
                            <div class="d-flex">
                                <input type="search" class="form-control form-control-sm bg-light px-3" placeholder="Temukan..." v-model="searchquery">
                                <button type="button" class="btn btn-sm btn-link text-decoration-none text-muted pl-3" @click="onOption" v-if="option">
                                    <i class="fa fa-bars"></i>
                                </button>
                            </div>
                        </div>
                        <hr class="m-0">
                    </div>
                    <div class="card-block overflow-auto" style="max-height: 300px;">
                        <div class="list-group p-2" v-if="items != null">
                            <div class="list-group-item list-group-item-action"
                                v-for="(item, i) in items"
                                :key="i" @click="selectItem(i)"
                                :class="{'active': isSelected(i) }">
                                <select-item :item="item" :img="img" :classname="'small'"/>
                            </div>
                        </div>
                        <div v-else>
                            <slot name="body"></slot>
                        </div>
                    </div>
                    <div class="card-block pt-3">
                        <div class="d-flex">
                            <button type="button" class="px-3 py-2 btn badge-danger btn-sm mx-auto" style="border-radius: 10rem 10rem 0 0" @click="dropdown.status = false">
                                <div class="d-flex flex-column justify-content-center">
                                    <span class="small">
                                        <i class="fa fa-chevron-up"></i>
                                    </span>
                                </div>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import SelectItem from './Select-Item'
export default {
    props: {
        items: {
            type: Array,
        },
        img: {
            type: Boolean,
            default: false
        },
        keyId: {
            type: String,
            required: true,
            default: "id",
        },
        search: {
            type: String,
            default: null,
        },
        value: {
            default: null
        },
        name: {
            type: String,
            required: true,
            default: '',
        },
        option: {
            type: Boolean,
            default: false,
        },
        absolute: {
            type: Boolean,
            default: false,
        },
        transition: {
            type: String,
            default: 'fly-down',
        }
    },
    data(){
        return {
            dropdown: {
                status: false
            },
            searchquery: ''
        }
    },
    computed: {
        selected: function(){
            let item = this.items.find(e=>this.getItemKey(e) == this.value );
            return item ? item : {};
        },
    },
    components: {
        'select-item': SelectItem
    },
    methods: {
        getItemKey(item){
            return item[ this.keyId ] != undefined ? item[ this.keyId ] : null;
        },
        getIndexByKey(value){
            return this.items.findIndex(item=>this.getItemKey(item) == value);
        },
        toggleDropdown: function(){
            this.dropdown.status = !this.dropdown.status;
            this.$emit('ontoggledropdown', this.dropdown.status);
        },
        selectItem: function(i){
            let value = this.value == this.getItemKey(this.items[i]) ? null : this.getItemKey(this.items[i]);
            this.dropdown.status = false;
            this.$emit("input", value);
            this.$emit("onselect", this.items[i]);
        },
        isSelected: function(i){
            return this.getItemKey(this.selected) == this.getItemKey(this.items[i]);
        },
        onOption: function(){
            this.$emit("onoption");
        }
    },
    watch: {
        searchquery: function(){
            this.$emit('onsearch', this.searchquery);
        },
        search: function(v, o){
            this.searchquery = v;
        },
    },
    created: function(){

    },
    mounted: function(){
        this.$nextTick(()=>{

        })
    }
}
</script>