<template>
    <div style="position-relative">
        <div class="position-relative reference" :style="referencestylecomputed" @click="toggleShow">
            <slot name="reference"></slot>
        </div>
        <transition :name="transition" @after-leave="afterleave" @before-enter="beforeenter">
            <div class="position-fixed popper" :class="[tooltipclass, 'fade show']" :style="[popperstyle, {width: width,}, popperstylecomputed]" v-if="show">
                <slot name="tooltip"></slot>
            </div>
        </transition>
    </div>
</template>
<script>
import Popper from 'popper.js'

export default {
    props: {
        placement: {
            type: String,
            default: "right",
        },
        default: {
            type: Boolean,
            default: false,
        },
        model: {
            type: Boolean,
            default: null,
        },
        tooltipclass: {
            default:""
        },
        active: {
            type: Boolean,
            default: false
        },
        tooltipstyle: {
            default: null,
        },
        referenceclass: {
            default:""
        },
        referencestyle: {
            default: null,
        },
        width: {
            default:"200px"
        },
        transition: {type: String, required: false},
        id: {
            type: String,
            default: function() {
                let now = new Date();
                return String(now.valueOf());
            }
        }
    },
    data(){
        return {
            status: false,
            popper: null,
            popperstyle: {},
        }
    },
    computed: {
        dataplacement: function() {
            return `${this.placement}-end`;
        },
        referencestylecomputed: function() {
            return this.referencestyle ? this.referencestyle : {
                'z-index': this.active || this.show ? '4' : '1',
            };
        },
        popperstylecomputed: function() {
            return this.tooltipstyle ? this.tooltipstyle : {
                'width': this.width,
                'z-index': this.active || this.show ? '3' : '1',
            }
        },
        show: {
            get(){
                return this.model != null ?  this.model : this.status;
            },
            set(val){
                if(val){
                    this.status = val;
                } else {
                    this.status = val;
                }
                this.$emit("ontoggle", {status: this.status, id: this.id});
            }
        },
    },
    methods: {
        afterleave(){
            this.destroyPopper();
        },
        beforeenter(){
            if(!this.popper){
                this.initPopper();
            }
        },
        toggleShow(){
            this.show = !this.show;
        },
        applyStyle: function(params) {
            var style = Object.assign({}, params.styles);
            var x = 0;
            if(this.placement == "left"){
                style.paddingLeft = `calc(${params.offsets.reference.width}px + .5rem)`;
                x = Math.round(params.offsets.reference.left)
            } else {
                style.paddingRight = `calc(${params.offsets.reference.width}px + .5rem)`;
                x = 0 - Math.round(params.offsets.popper.width - (params.offsets.reference.left + params.offsets.reference.width))
            }
            let y = Math.round(
                params.offsets.popper.top
            );
            let z = Math.round(0);
            style.transform = `translate3d(${x}px, ${y}px, ${z})`;
            this.popperstyle = style;
            this.$emit("onupdate", params.offsets);
        },
        updatePopper(){
            if(this.popper)
                this.popper.update();
        },
        initPopper(){
            this.$nextTick(()=>{
                this.popper = new Popper(this.$el.querySelector("div.reference"), this.$el.querySelector("div.popper"),{
                    placement: this.placement,
                    onCreate: (e)=>{
                        this.updatePopper();  
                    },
                    modifiers: {
                        applyStyle: {
                            enabled: false
                        },
                        applyStyle: {
                            enabled: true,
                            fn: this.applyStyle,
                        },
                    },
                    positionFixed: true
                });
            })
        },
        destroyPopper(){
            if(this.popper)
                this.$nextTick(()=>{
                    this.popper.destroy();
                    this.popper = null;
                    this.popperstyle = {};
                })
        }
    },
    watch: {
        placement: function() {
            this.updatePopper();
        },
        width: function() {
            this.updatePopper();
        }
    },
    updated: function(e) {
    },
    created: function() {
        this.$nextTick(_=>{
            this.show = this.default;
        });
    },
    beforeMount(){
    },
    mounted: function() {
        this.$nextTick(_=>{
            if(this.show)
                this.initPopper();
        })
    },
    destroyed(){
        if(this.popper)
            this.destroyPopper();
    }
}
</script>