<template>
    <div>
        <div class="table-responsive">
            <table class="table table-borderless mb-0">
                <thead>
                    <slot name="header" :items="value" :sort="sort"></slot>
                </thead>
                
                <tbody>
                    <template v-if="loading_status">
                        <tr>
                            <td colspan="100" class="p-0">
                                <slot name="loading">
                                    <div class="w-100 table-loading rounded-0"></div>
                                </slot>
                            </td>
                        </tr>
                    </template>
                </tbody>

                <tbody :class="{ disabled: loading_status }">
                    <template v-for="(item, i) in value">
                        <slot name="before" :item="item" :index="i" :clickEvent="itemClick" :sort="sort" :removeEvent="removeItem"></slot>
                        <tr :key="item.id">
                            <slot :item="item" :index="i" :clickEvent="itemClick" :sort="sort" :removeEvent="removeItem"></slot>
                        </tr>
                        <slot name="after" :item="item" :index="i" :clickEvent="itemClick" :sort="sort" :removeEvent="removeItem"></slot>
                    </template>
                </tbody>
                <thead v-if="!headerTop">
                    <slot name="header" :items="value" :sort="sort"></slot>
                </thead>
            </table>
        </div>
        <div class="border-bottom row m-0 justify-content-between" style="overflow: hidden">
            <div class="col-md">
                <slot name="table-bottom"></slot>
            </div>
            <div v-if="pagination_status" class="col-md d-flex justify-content-end">
                <pagination :pagination="pagination" :max="pagination.max" v-on:page="onPage"/>
            </div>
        </div>
    </div>
</template>
<script>

import tableHead from './components/TableHead'
import Pagination from '../pagination/Pagination'

import EddData from './libs/data'

export default {
    /* 
        @install
        called when only it treat as plugin

        register all components it needed
    */
    install: function(Vue){
        Vue.component('v-table', this)
        Vue.component('table-head', tableHead)
        Vue.component('table-pagination', Pagination)

        if(window.eddtable === undefined)
            window.eddtable = {
                data: EddData
            }
    },
    components: {
        Pagination
    },
    props: {
        /* 
            @param  value   { Array }
                array data for table, each item { Object } of this array must
                have on requirement @id key
        */
        value: {
            type: Array,
            required: true
        },
        
        /* 
            @param  sort    { Object }  @Nullable
                props to control the sorter navigation, it is must an object
                type that contains two keys:
                    @key    column  { String }
                    @key    asc     { Boolean }
        */
        sort: {
            type: Object,
            default: null
        },
        
        pagination: {
            current_page: {
                type: Number,
                default: 1,
            },
            last_page: {
                type: Number,
                default: null,
            },
            max: {
                type: Number,
                default: null
            },

            default(){
                return {}
            }
        },
        headerTop: {
            type: Boolean,
            default: false
        },
        eddData: {
            type: Object,
            default: null
        }
    },
    computed: {
        /* 
            @property   sortable    { Boolean }
                the state of sorter navigation status, it will return true if
                the @sort parsed from props
        */
        sortable: function(){
            return this.sort !== null
        },

        pagination_status: function(){
            return this.pagination.current_page !== undefined && this.pagination.last_page
        },

        edd_data_status: function(){
            return this.eddData !== null
        },
        loading_status: function(){
            return this.edd_data_status ? this.eddData.loading : false
        },
        sorter: function(){
            return this.sort
        }
    },
    provide(){
        /* 
            @key    sortable    { Boolean }
            @key    sort_object    { Object }   @Nullable
        */
        return {
           $sortable: () => this.sortable,
           $sort_object: () => this.sorter
        }
    },
    methods: {
        /* 
            Item Event
                this methods bellow will be bind as scope slot

            @method itemClick() { void }
                called when the clicEvent was trigger
            @param  data { Mixed }
                anything parsed will return to @emit item-click event
        */
        itemClick(data){
            this.$emit("item-click", data)
        },
        removeItem(i){
            if(typeof i === 'number' && this.value[ i ] !== undefined){
                let newvalue = this.value.slice()
                newvalue.splice(i, 1);
                this.$emit('input', newvalue);
            }
        },

        /* 
            Registered Event
                this event will be called from child component
            @method     onSort { void }
                onSort mehtod called when the child component emit the @event sortAction
            @params     items   { Object }
                items params must contains some requirement keys like:
                @key    id      { Number | String }
                @key    data    { Object }
                    @key    asc     { Boolean }
                    @key    column  { String }
        */
        onSort(items){
            this.$emit("sort", {
                id: items.id,
                column: items.column,
                data: {
                    column: items.target,
                    asc: items.data.asc,
                },
                old: items.old
            })
        },
        /* 
            @method     onOption    { void }
                will trigger when the child component emit the optionAction Event
        */
        onOption(data){
            this.$emit("option", data)
        },

        onPage(data){
            this.$emit("page", data)
        }
    },
    mounted(){
    },
    created(){
        /* 
            register event emit here
        */
        this.$on("sortAction", this.onSort)
        this.$on("optionAction", this.onOption)
    }
}
</script>
<style lang="scss" scoped>
    @import './styles/main';
</style>