<template>
    <div class="mb-1">
        <label class="mb-0">
            <div class="custom-control custom-checkbox badge" style="font-size: 100%">
                <input type="checkbox" class="custom-control-input" v-model="status" @change="onToggle($event)">
                <span class="custom-control-label font-weight-normal" style="font-size: 100%">
                    <span class="small font-weight-normal">
                        {{item.label}} <span class="text-muted ml-3" v-if="item.description">{{ item.description }}</span>
                    </span>
                </span>
            </div>
        </label>
        <div class="" v-if="item.child && item.checked">
            <div v-for="(c, i) in item.child" :key="i" class="text-muted badge badge-light font-weight-normal px-2 py-1 m-1 badge-pill" style="font-size: 100%">
                {{ c }}
            </div>
        </div>
    </div>
</template>
<script>

export default {
    name: "CheckListItem",
    props: {
        item: {
            type: Object,
            required: true
        },
        onChange: {}
    },
    components: {
    },
    computed: {
        status: {
            set: function(v){
                this.checked = v;
            },
            get: function(){
                return this.checked;
            }
        },

    },
    data: function(){
        return {
            checked: null,
        }
    },
    methods: {
        onToggle: function(e){
            let obj = Object.assign({}, this.item);
            obj.checked = this.status;
            this.$emit('onchange', obj);
        }
    },
    watch: {
        item: {
            deep: true,
            handler: function(){
                this.checked = this.item.checked ? true : false;
            }
        }
    },
    created: function(){
        this.checked = this.item.checked ? true : false;
    }
}
</script>