<template>
    <div>
        <div class="dropdown-item-text small py-3" v-if="label">
            <div class="badge badge-pill badge-primary px-3 py-2" style="font-size: 100%">
                <span class="mr-3" v-if="icon">
                    <i :class="icon"></i>    
                </span>
                {{ label }}
            </div>
        </div>
        <div v-for="(item, i) in items" :key="i.label" class="dropdown-item-text">
            <checklistitem :item="item" @onitemchange="itemChange($event)" :transition="transition"></checklistitem>
        </div>
    </div>
</template>
<script>
import CheckListLabel from './CheckListLabel';

export default {
    name: "CheckList",
    props: {
        label: {
            type: String,
            required: false,
            default: null
        },
        icon: {
            type: String,
            required: false,
            default: null
        },
        items: {
            type: Array,
            required: true,
            default: ''
        },
        transition: {
            type: String,
            default: 'fade'
        },
    },
    components: {
        checklistitem: CheckListLabel,
    },
    data: function(){
        return {
            itemsLabel: [],
        }
    },
    methods: {
        itemChange: function(e){
            var e = Object.assign({}, e);
            let a = this.itemsLabel.map((el, i)=>{
                let o = Object.assign({}, el);
                return o.label == e.label ? e : o;
            });
            this.itemsLabel = a;
            this.$emit("onitemchange", this.itemsLabel.slice());
        }
    },
    created: function(){
        this.itemsLabel = this.items;
    }
}
</script>