<template>
    <td class="py-4">
        <div class="d-flex justify-content-between" v-if="!empty">
            <div class="d-flex h-100">
                <div class="mr-2 text-muted" v-if="$sortable() && !unsorted">
                    <slot name="icon" :asc="is_desc" :desc="is_asc">
                        <table-sort-icon :asc="is_asc" :desc="is_desc"></table-sort-icon>
                    </slot>
                </div>
                <a href="javascript:void(0)" class="small text-decoration-none text-dark" v-on:click="labelClick($event)">
                    <div class="h-100 d-flex flex-column justify-content-center">
                        <slot> {{ text }} </slot>
                    </div>
                </a>
            </div>
            <div class="d-none d-sm-block ml-2" v-if="option">
                <div class="h-100 d-flex flex-column">
                    <a href="javascript:void(0)" class="ml-2 text-muted" v-on:click="optionClick($event)">
                        <slot name="head-info">
                            <svg class="bi bi-funnel" width=".8em" height=".8em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M1.5 1.5A.5.5 0 012 1h12a.5.5 0 01.5.5v2a.5.5 0 01-.128.334L10 8.692V13.5a.5.5 0 01-.342.474l-3 1A.5.5 0 016 14.5V8.692L1.628 3.834A.5.5 0 011.5 3.5v-2zm1 .5v1.308l4.372 4.858A.5.5 0 017 8.5v5.306l2-.666V8.5a.5.5 0 01.128-.334L13.5 3.308V2h-11z" clip-rule="evenodd"/>
                            </svg>
                        </slot>
                    </a>
                </div>
            </div>
        </div>
    </td>
</template>
<script>

import TableSortIcon from './TableSortIcon'

export default {
    components: {
        TableSortIcon
    },
    props: {

        /* 
            @param  id  { String }
        */
        id: {
            required: true,
        },
        
        /* 
            @param {String} column 
        */
        column: {
            type: String,
            default: null
        },
        
        /* 
            @param {Boolean} option

            set true to show the option / filter icon
        */
        option: {
            type: Boolean,
            default: false
        },

        /* 
            @param {String} text

            when it prop are parsed, it will be the default label of the default
            props
        */
        text: {
            default: null
        },
        unsorted: {
            type: Boolean,
            default: false,
        }
    },
    data(){
        return {
            /* 
                if true the head label will be empty
            */
            empty: false,
        }
    },
    computed: {

        /* 
            @var {Boolean} is_asc

            return true if the data sort is asc and the sort_status is true
        */
        is_asc(){
            return this.sort_status && this.data_sort.asc === true
        },

        /* 
            @var {Boolean} is_desc

            return true if the data sort is not asc and the sort_status is true
        */
        is_desc(){
            return this.sort_status && this.data_sort.asc === false
        },

        /* 
            @var {String} target

            the target to send to the parent, if the column is set then will send the parent
            or if not then the id
        */
        target(){
            return this.column !== null ? this.column : this.id
        },

        /* 
            @var {Boolean} sort_status

            return true if the inject sortable is true and the target is equals to  data_sort's column
        */
        sort_status(){
            return this.$sortable() && this.data_sort.column !== undefined ? this.target === this.data_sort.column : false
        },

        /* 
            return of the new object of the inject sort_object variable
        */
        data_sort(){
            return this.$sort_object()
        },
    },
    methods: {
        /* 
            check the slots if has no slots and the props text no set then the empty
            model will set to true
        */
        checkSlot(){
            this.empty = Object.getOwnPropertyNames(this.$slots).length || this.text ? false : true;
        },

        /* 
            will trigered when the label is clicked, then send the data sort by emiting the
            sortAction the parent's event
            
            @param {Object} el
        */
        
        labelClick(el){
            this.$parent.$emit("sortAction", {
                id: this.id,
                target: this.target,
                old: Object.assign({}, this.sort_object),
                data: {
                    asc: this.is_asc ? false : true,
                    column: this.column
                }
            })
        },
        
        /* 
            will trigered when the option the right side of cell head was click, then emit the optionClick
            parent event

            @param {Object} el
        */
        optionClick(el){
            let data = {
                id: this.id,
                el: el.target,
                event: Object.assign({}, el)
            };
            this.$emit("optionClick", data)
            this.$parent.$emit("optionAction", data)
        }
    },
    updated(){
        /* 
            check the slot when it is update
        */
        this.checkSlot()
    },
    inject: {
        /* 
            sortable status from parent component, if no sort object bind to parent then
            it will set to be false
        */
        '$sortable': {
            default: function(){
                return false
            }
        },

        /* 
            the object binded to parent
        */
        '$sort_object': {
            default: function(){
                return {}
            }
        }
    },
    mounted(){
        /* 
            when the component already mounted into the dom then check the slot
        */
        this.checkSlot()
    }
}
</script>
<style lang="scss" scoped>
    @import './../styles/table_head';
</style>