<template>
    <div>
        <label>
            <div class="custom-control custom-checkbox">
                <input type="checkbox" class="custom-control-input" v-model="status" @change="onChange($event)">
                <span class="custom-control-label">
                    <span class="small font-weight-normal">
                        {{item.label}} <span v-if="item.description" class="text-muted ml-3">{{ item.description }}</span>
                    </span>
                </span>
            </div>
        </label>
        <div v-if="item.child">
            <transition-group :name="transition">
                <CheckListSubItem :items="item.child" @onitemchange="itemChange($event)" v-if="item.checked" :key="1" :transition="transition"></CheckListSubItem>
                <div class="small text-muted dropdown-item-text" v-else :key="2">
                    {{ item.child.length }} item
                </div>
            </transition-group>
        </div>
    </div>
</template>
<script>
import CheckListSubItem from './CheckListSubItem';

export default {
    name: "CheckListLabel",
    props: {
        item: {
            type: Object,
            required: true
        },
        transition: {
            type: String,
            default: 'fade'
        },
    },
    components: {
        CheckListSubItem: CheckListSubItem
    },
    data: function(){
        return {
            checked: false,
            itemLabelStore: {},
        }
    },
    computed: {
        itemLabel: {
            get: function(){
                return Object.assign({}, this.item);
            },
            set: function(v){
                this.itemLabelStore = v;
            }
        },
        status: {
            get: function(){
                return this.checked;
            },
            set: function(v){
                this.checked = v;
            }
        },
    },
    methods: {
        update: function(obj){
            this.itemLabel = Object.assign({}, obj);
            this.$emit("onitemchange", obj);
        },
        itemChange: function(e){
            let a = e.slice();
            let o = Object.assign({}, this.itemLabel);
            o.child = a;
            this.update(o);
        },
        onChange: function(e){
            let o = Object.assign({}, this.itemLabel);
            o.checked = this.status;
            if(this.status)
                this.checkAllChild(o);
            else
                this.checkAllChild(o, false);
        },
        checkAllChild: function(obj, checked = true){
            if(Array.isArray(obj.child)){
                let arr = obj.child.map((e, i)=>{
                    e = Object.assign({}, e);
                    e.checked = checked;
                    return e;
                });
                obj.child = arr;
            }
            this.update(obj);
        }
    },
    watch: {
        item: {
            deep: true,
            handler: function(){
                this.checked = this.item.checked ? true : false;
            }
        }
    },
    created: function(){
        this.checked = this.item.checked ? true : false;
    }
}
</script>