<template>
    <div class="alert shadow" :class="[notif.className, {'bg-dark text-white': !notif.className}]" @click="containerClick($event, notif.id)">
        <div class="d-flex">
            <div class="d-flex">
                <div class="py-2 pr-3" v-if="notif.icon">
                    <i class="fa" :class="[notif.icon]"></i>
                </div>
                <div>
                    <div @click="containerClick($event, notif.id)" class="small font-weight-bold">
                        {{ notif.label }}
                    </div>
                    <div class="small">
                        {{ notif.description }}
                    </div>
                </div>
            </div>
            <div class="ml-auto d-flex" v-if="notif.buttons">
                <div class="px-1 d-flex flex-column justify-content-center" :key="i" v-for="(button, i) in notif.buttons">
                    <button class="btn btn-sm px-3" :class="[button.className, {'btn-dark badge-pill': !button.className}]" @click="buttonClick(button)">
                        <span class="mr-1" v-if="button.icon">
                            <i class="fa" :class="button.icon"></i>
                        </span>
                        <span>
                            {{ button.text }}
                        </span>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        notif: {
            type: Object,
            required: true,
        }
    },
    data: function(){
        return {
            timeout: null
        }
    },
    methods: {
        containerClick: function(e, id){
            if(typeof this.notif.action == "function")
                this.notif.action(this, id);
        },
        remove: function(){
            this.$notification.remove(this.notif.id);
        },
        buttonClick: function(button){
            if(typeof button.action == "function")
                button.action(this);
        }
    },
    mounted: function(){
        this.$nextTick(()=>{
            if(this.notif.time)
                this.timeout = setTimeout(()=>{ this.remove() }, this.notif.time);
        })
    },
    created: function(){
    },
    destroyed: function(){
        clearTimeout(this.timeout);
    }
}
</script>